/* 基础样式重置 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 25%, #9b59b6 50%, #667eea 75%, #764ba2 100%);
  color: #2c3e50;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

/* 应用主容器 */
.app {
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 25%, #9b59b6 50%, #667eea 75%, #764ba2 100%);
}

/* 背景动画 */
.background-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.gradient-orb {
  position: absolute;
  border-radius: 50%;
  filter: blur(120px);
  opacity: 0.3;
  animation: float 25s ease-in-out infinite;
}

.orb-1 {
  width: 600px;
  height: 600px;
  background: radial-gradient(circle, rgba(255,107,157,0.4) 0%, rgba(196,69,105,0.2) 100%);
  top: -300px;
  left: -300px;
  animation-delay: 0s;
}

.orb-2 {
  width: 400px;
  height: 400px;
  background: radial-gradient(circle, rgba(155,89,182,0.4) 0%, rgba(102,126,234,0.2) 100%);
  top: 30%;
  right: -200px;
  animation-delay: -10s;
}

.orb-3 {
  width: 700px;
  height: 700px;
  background: radial-gradient(circle, rgba(118,75,162,0.4) 0%, rgba(196,69,105,0.2) 100%);
  bottom: -350px;
  left: 50%;
  transform: translateX(-50%);
  animation-delay: -5s;
}

@keyframes float {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  33% { transform: translate(30px, -30px) rotate(120deg); }
  66% { transform: translate(-20px, 20px) rotate(240deg); }
}

/* Header样式 */
.header {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.logo-icon {
  color: white;
  animation: pulse 2s ease-in-out infinite;
  filter: drop-shadow(0 2px 8px rgba(255, 255, 255, 0.3));
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

.logo-text {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}

.chain-badge {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.wallet-section {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* Language Switcher */
.language-switcher {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 0.75rem 1rem;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 80px;
  justify-content: center;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.language-switcher:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.3);
  border-color: rgba(255, 107, 157, 0.3);
}

.language-icon {
  font-size: 1.1rem;
}

.language-text {
  font-size: 0.85rem;
  letter-spacing: 0.05em;
}

.connect-btn, .get-started-btn {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.connect-btn:hover, .get-started-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.15);
  border-color: rgba(255, 255, 255, 0.5);
}

.connect-btn:disabled, .get-started-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.wallet-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.account-display {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.account-text {
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
}

.disconnect-btn {
  background: rgba(231, 76, 60, 0.2);
  border: 1px solid rgba(231, 76, 60, 0.5);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  backdrop-filter: blur(10px);
}

.disconnect-btn:hover {
  background: rgba(231, 76, 60, 0.4);
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.3);
  border-color: rgba(231, 76, 60, 0.8);
}

/* 主内容区域 */
.main-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  min-height: calc(100vh - 140px);
}

/* 欢迎页面 */
.welcome-section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
}

.welcome-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 32px;
  padding: 3rem;
  text-align: center;
  max-width: 800px;
  width: 100%;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.welcome-header {
  margin-bottom: 3rem;
}

.welcome-icon {
  margin-bottom: 2rem;
}

.icon-bg {
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  border: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.rotating-icon {
  animation: rotate 3s linear infinite;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.welcome-card h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: white;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.welcome-card p {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 2rem;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.feature-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.feature-item:hover {
  transform: translateY(-5px);
  background: rgba(255, 255, 255, 0.08);
}

.feature-icon {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.feature-item h3 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #00d4ff;
}

.feature-item p {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
}

/* 仪表板 */
.dashboard {
  animation: fadeInUp 0.6s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}

/* 卡片通用样式 */
.claim-card, .rewards-card, .stats-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 24px;
  padding: 2rem;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.claim-card:hover, .rewards-card:hover, .stats-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.15);
  border-color: rgba(255, 255, 255, 0.4);
  background: rgba(255, 255, 255, 0.15);
}

.card-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.card-icon {
  color: white;
  filter: drop-shadow(0 2px 8px rgba(255, 255, 255, 0.3));
}

.card-header h2 {
  font-size: 1.25rem;
  font-weight: 700;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* 领取卡片 */
.claim-info {
  margin-bottom: 1.5rem;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.info-row:last-child {
  border-bottom: none;
}

.label {
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
}

.value {
  font-weight: 700;
  color: white;
  font-family: 'Courier New', monospace;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

.time-left {
  color: #ff6b9d;
  font-weight: 700;
  animation: pulse-time 2s ease-in-out infinite;
  text-shadow: 0 2px 8px rgba(255, 107, 157, 0.5);
}

@keyframes pulse-time {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

.referrer-section {
  margin-bottom: 1.5rem;
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.referrer-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  color: #00d4ff;
  font-weight: 600;
  font-size: 1rem;
}

.referrer-tip {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
  margin-bottom: 1rem;
  line-height: 1.4;
}

.referrer-input-group {
  margin-bottom: 1rem;
}

.referrer-input {
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 0.75rem;
  color: white;
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  transition: all 0.3s ease;
}

.referrer-input:focus {
  outline: none;
  border-color: #00d4ff;
  box-shadow: 0 0 0 2px rgba(0, 212, 255, 0.2);
}

.referrer-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.bind-btn {
  width: 100%;
  background: linear-gradient(135deg, #00d4ff 0%, #667eea 100%);
  border: none;
  color: white;
  padding: 0.75rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.bind-btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.bind-btn:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
}

.referrer-note {
  background: rgba(255, 193, 7, 0.1);
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 8px;
  padding: 0.75rem;
  font-size: 0.8rem;
  color: rgba(255, 193, 7, 0.9);
  text-align: center;
}

.claim-btn {
  width: 100%;
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 50%, #9b59b6 100%);
  border: none;
  color: white;
  padding: 1rem 2rem;
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-top: 1rem;
  box-shadow: 0 8px 25px rgba(255, 107, 157, 0.4);
  position: relative;
  overflow: hidden;
}

.claim-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #ff8fab 0%, #d63581 50%, #a569bd 100%);
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(255, 107, 157, 0.5);
}

.claim-btn.disabled, .claim-btn:disabled {
  background: linear-gradient(135deg, #95a5a6 0%, #7f8c8d 100%);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
  opacity: 0.7;
}

.claim-stats {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
}

/* 奖励卡片 */
.rewards-grid {
  display: grid;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.reward-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem;
  border-radius: 16px;
  display: flex;
  align-items: center;
  gap: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.reward-item:hover {
  background: rgba(255, 255, 255, 0.08);
}

.reward-icon {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.eth-reward {
  background: linear-gradient(135deg, #627eea 0%, #764ba2 100%);
}

.token-reward {
  background: linear-gradient(135deg, #00d4ff 0%, #667eea 100%);
}

.reward-info {
  flex: 1;
}

.reward-info h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  color: #00d4ff;
}

.reward-amount {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.total-earned {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
}

.reward-claim-btn {
  background: linear-gradient(135deg, #00d4ff 0%, #667eea 100%);
  border: none;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 60px;
}

.reward-claim-btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.reward-claim-btn.disabled, .reward-claim-btn:disabled {
  background: rgba(255, 255, 255, 0.1);
  cursor: not-allowed;
  transform: none;
}

.reward-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
}

.stat-card {
  background: rgba(255, 255, 255, 0.05);
  padding: 1rem;
  border-radius: 12px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-label {
  display: block;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 0.5rem;
}

.stat-value {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  color: #00d4ff;
}

/* 统计卡片 */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.stat-item {
  text-align: center;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-number {
  font-size: 1.5rem;
  font-weight: 700;
  color: #00d4ff;
  margin-bottom: 0.25rem;
}

.stat-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
}

.referrer-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  margin-bottom: 1.5rem;
}

.referrer-label {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9rem;
}

.referrer-address {
  font-family: 'Courier New', monospace;
  color: #00d4ff;
  font-weight: 600;
}

.referral-section {
  background: rgba(255, 255, 255, 0.05);
  padding: 1.5rem;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.referral-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  color: #00d4ff;
  font-weight: 600;
}

.referral-link-container {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.referral-link-input {
  flex: 1;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 0.75rem;
  color: white;
  font-size: 0.85rem;
  font-family: 'Courier New', monospace;
}

.copy-btn {
  background: linear-gradient(135deg, #00d4ff 0%, #667eea 100%);
  border: none;
  color: white;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  font-size: 0.85rem;
  font-weight: 600;
  white-space: nowrap;
}

.copy-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(0, 212, 255, 0.3);
}

.copy-btn.copied {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
}

.referral-tip {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.4;
}

/* 加载状态 */
.loading-overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  gap: 1rem;
}

.loading-spinner {
  width: 24px;
  height: 24px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top: 3px solid #00d4ff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-spinner.small {
  width: 16px;
  height: 16px;
  border-width: 2px;
}

.spinning {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 离线模式 */
.offline-mode {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.offline-notice {
  text-align: center;
  background: rgba(13, 15, 23, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 3rem;
  max-width: 400px;
}

.offline-notice h2 {
  margin: 1rem 0;
  color: #00d4ff;
}

/* 错误边界样式 */
.error-boundary {
  min-height: 100vh;
  background: #0a0b0f;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.error-content {
  background: rgba(13, 15, 23, 0.9);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 3rem;
  max-width: 600px;
  width: 100%;
  text-align: center;
}

.error-icon {
  color: #ff6b6b;
  margin-bottom: 2rem;
}

.error-content h1 {
  color: #ff6b6b;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.error-content p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  margin-bottom: 2rem;
  line-height: 1.6;
}

.error-solutions {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  text-align: left;
}

.error-solutions h3 {
  color: #00d4ff;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.error-solutions ul {
  list-style: none;
  padding: 0;
}

.error-solutions li {
  color: rgba(255, 255, 255, 0.8);
  padding: 0.5rem 0;
  padding-left: 1.5rem;
  position: relative;
}

.error-solutions li::before {
  content: "•";
  color: #00d4ff;
  position: absolute;
  left: 0;
  font-weight: bold;
}

.refresh-btn {
  background: linear-gradient(135deg, #00d4ff 0%, #667eea 100%);
  border: none;
  color: white;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  margin: 0 auto 2rem;
  font-size: 1rem;
}

.refresh-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.3);
}

.error-details {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 1rem;
  margin-top: 2rem;
  text-align: left;
}

.error-details summary {
  color: #00d4ff;
  cursor: pointer;
  font-weight: 600;
  margin-bottom: 1rem;
}

.error-details pre {
  background: rgba(0, 0, 0, 0.3);
  padding: 1rem;
  border-radius: 8px;
  overflow-x: auto;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
  white-space: pre-wrap;
  word-break: break-word;
}

/* Footer */
.footer {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  padding: 2rem 0;
  margin-top: 4rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 -4px 20px rgba(255, 107, 157, 0.1);
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.social-links {
  display: flex;
  gap: 1.5rem;
}

.social-link {
  color: white;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.social-link:hover {
  color: #ff6b9d;
  transform: translateY(-2px);
  text-shadow: 0 4px 12px rgba(255, 107, 157, 0.4);
}

/* 响应式设计 */
@media (max-width: 768px) {
  .main-content {
    padding: 1rem;
  }
  
  .header-content {
    padding: 1rem;
    flex-direction: column;
    gap: 1rem;
  }
  
  .logo {
    order: 1;
  }
  
  .wallet-section {
    order: 2;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .language-switcher {
    order: 1;
    margin-bottom: 0.5rem;
  }
  
  .cards-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .welcome-card {
    margin: 1rem;
    padding: 2rem;
  }
  
  .welcome-card h1 {
    font-size: 2rem;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .referral-link-container {
    flex-direction: column;
  }
  
  .footer-content {
    flex-direction: column;
    text-align: center;
  }
  
  .rewards-grid {
    grid-template-columns: 1fr;
  }
  
  .reward-item {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .reward-stats {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .welcome-card {
    padding: 1.5rem;
  }
  
  .claim-card, .rewards-card, .stats-card {
    padding: 1.5rem;
  }
  
  .card-header h2 {
    font-size: 1.1rem;
  }
  
  .logo-text {
    font-size: 1.25rem;
  }
}